'use client';

import { useEffect, useState } from 'react';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Badge } from '@/components/ui/badge';
import { Worker, Attendance } from '@/lib/types';
import { 
  getWorkers, 
  getAttendanceRecord, 
  addAttendanceRecord, 
  updateAttendanceRecord,
  generateId,
  formatDate,
  formatTime,
  calculateHours
} from '@/lib/storage';

export default function AttendancePage() {
  const [workers, setWorkers] = useState<Worker[]>([]);
  const [attendanceRecords, setAttendanceRecords] = useState<Record<string, Attendance>>({});
  const [loading, setLoading] = useState(true);
  const [processingWorker, setProcessingWorker] = useState<string>('');
  const today = formatDate(new Date());

  useEffect(() => {
    loadAttendanceData();
  }, []);

  const loadAttendanceData = () => {
    try {
      const workersData = getWorkers().filter(w => w.isActive);
      setWorkers(workersData);

      // Load today's attendance for all workers
      const records: Record<string, Attendance> = {};
      workersData.forEach(worker => {
        const record = getAttendanceRecord(worker.id, today);
        if (record) {
          records[worker.id] = record;
        }
      });
      setAttendanceRecords(records);
    } catch (error) {
      console.error('Error loading attendance data:', error);
    } finally {
      setLoading(false);
    }
  };

  const handleCheckIn = async (worker: Worker) => {
    setProcessingWorker(worker.id);
    try {
      const now = new Date();
      const checkInTime = formatTime(now);

      const newRecord: Attendance = {
        id: generateId(),
        workerId: worker.id,
        date: today,
        checkIn: checkInTime,
        checkOut: null,
        totalHours: 0,
        status: 'present'
      };

      addAttendanceRecord(newRecord);
      setAttendanceRecords(prev => ({
        ...prev,
        [worker.id]: newRecord
      }));
    } catch (error) {
      console.error('Error checking in worker:', error);
      alert('Failed to check in worker. Please try again.');
    } finally {
      setProcessingWorker('');
    }
  };

  const handleCheckOut = async (worker: Worker) => {
    const existingRecord = attendanceRecords[worker.id];
    if (!existingRecord || !existingRecord.checkIn) {
      alert('Worker must check in first');
      return;
    }

    setProcessingWorker(worker.id);
    try {
      const now = new Date();
      const checkOutTime = formatTime(now);
      const totalHours = calculateHours(existingRecord.checkIn, checkOutTime);

      const updatedRecord: Attendance = {
        ...existingRecord,
        checkOut: checkOutTime,
        totalHours,
        status: totalHours >= 4 ? 'present' : 'half-day'
      };

      updateAttendanceRecord(updatedRecord);
      setAttendanceRecords(prev => ({
        ...prev,
        [worker.id]: updatedRecord
      }));
    } catch (error) {
      console.error('Error checking out worker:', error);
      alert('Failed to check out worker. Please try again.');
    } finally {
      setProcessingWorker('');
    }
  };

  const markAbsent = async (worker: Worker) => {
    setProcessingWorker(worker.id);
    try {
      const absentRecord: Attendance = {
        id: generateId(),
        workerId: worker.id,
        date: today,
        checkIn: null,
        checkOut: null,
        totalHours: 0,
        status: 'absent'
      };

      addAttendanceRecord(absentRecord);
      setAttendanceRecords(prev => ({
        ...prev,
        [worker.id]: absentRecord
      }));
    } catch (error) {
      console.error('Error marking worker absent:', error);
      alert('Failed to mark worker absent. Please try again.');
    } finally {
      setProcessingWorker('');
    }
  };

  const getWorkerStatus = (worker: Worker) => {
    const record = attendanceRecords[worker.id];
    if (!record) return 'not-marked';
    if (record.status === 'absent') return 'absent';
    if (record.checkIn && !record.checkOut) return 'checked-in';
    if (record.checkIn && record.checkOut) return 'checked-out';
    return 'not-marked';
  };

  const getStatusBadge = (status: string) => {
    switch (status) {
      case 'checked-in':
        return <Badge className="bg-blue-100 text-blue-800">Checked In</Badge>;
      case 'checked-out':
        return <Badge className="bg-green-100 text-green-800">Completed</Badge>;
      case 'absent':
        return <Badge className="bg-red-100 text-red-800">Absent</Badge>;
      default:
        return <Badge className="bg-gray-100 text-gray-800">Not Marked</Badge>;
    }
  };

  const getTodayStats = () => {
    const totalWorkers = workers.length;
    const markedWorkers = Object.keys(attendanceRecords).length;
    const presentWorkers = Object.values(attendanceRecords).filter(r => r.status === 'present' || r.checkIn).length;
    const absentWorkers = Object.values(attendanceRecords).filter(r => r.status === 'absent').length;
    const totalHours = Object.values(attendanceRecords).reduce((sum, r) => sum + r.totalHours, 0);

    return {
      totalWorkers,
      markedWorkers,
      presentWorkers,
      absentWorkers,
      totalHours,
      unmarkedWorkers: totalWorkers - markedWorkers
    };
  };

  if (loading) {
    return (
      <div className="flex items-center justify-center min-h-64">
        <div className="text-lg text-gray-600">Loading attendance data...</div>
      </div>
    );
  }

  const stats = getTodayStats();

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="flex justify-between items-center">
        <div>
          <h1 className="text-3xl font-bold text-gray-900">Daily Attendance</h1>
          <p className="text-gray-600 mt-1">
            {new Date().toLocaleDateString('en-US', { 
              weekday: 'long', 
              year: 'numeric', 
              month: 'long', 
              day: 'numeric' 
            })}
          </p>
        </div>
        <div className="flex gap-3">
          <Button variant="outline" asChild>
            <a href="/attendance/history">View History</a>
          </Button>
          <Button variant="outline" asChild>
            <a href="/attendance/reports">Reports</a>
          </Button>
        </div>
      </div>

      {/* Stats Cards */}
      <div className="grid grid-cols-2 md:grid-cols-5 gap-4">
        <Card>
          <CardContent className="pt-6">
            <div className="text-2xl font-bold">{stats.totalWorkers}</div>
            <p className="text-xs text-gray-600">Total Workers</p>
          </CardContent>
        </Card>
        <Card>
          <CardContent className="pt-6">
            <div className="text-2xl font-bold text-green-600">{stats.presentWorkers}</div>
            <p className="text-xs text-gray-600">Present</p>
          </CardContent>
        </Card>
        <Card>
          <CardContent className="pt-6">
            <div className="text-2xl font-bold text-red-600">{stats.absentWorkers}</div>
            <p className="text-xs text-gray-600">Absent</p>
          </CardContent>
        </Card>
        <Card>
          <CardContent className="pt-6">
            <div className="text-2xl font-bold text-yellow-600">{stats.unmarkedWorkers}</div>
            <p className="text-xs text-gray-600">Unmarked</p>
          </CardContent>
        </Card>
        <Card>
          <CardContent className="pt-6">
            <div className="text-2xl font-bold text-blue-600">{stats.totalHours.toFixed(1)}</div>
            <p className="text-xs text-gray-600">Total Hours</p>
          </CardContent>
        </Card>
      </div>

      {/* Workers List */}
      {workers.length > 0 ? (
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
          {workers.map((worker) => {
            const status = getWorkerStatus(worker);
            const record = attendanceRecords[worker.id];
            const isProcessing = processingWorker === worker.id;

            return (
              <Card key={worker.id} className="hover:shadow-md transition-shadow">
                <CardHeader className="pb-3">
                  <div className="flex justify-between items-start">
                    <div>
                      <CardTitle className="text-lg">{worker.name}</CardTitle>
                      <p className="text-sm text-gray-600">{worker.role}</p>
                    </div>
                    {getStatusBadge(status)}
                  </div>
                </CardHeader>
                <CardContent className="space-y-4">
                  {record && (
                    <div className="space-y-2 text-sm">
                      {record.checkIn && (
                        <div className="flex justify-between">
                          <span className="text-gray-600">Check In:</span>
                          <span className="font-medium">{record.checkIn}</span>
                        </div>
                      )}
                      {record.checkOut && (
                        <div className="flex justify-between">
                          <span className="text-gray-600">Check Out:</span>
                          <span className="font-medium">{record.checkOut}</span>
                        </div>
                      )}
                      {record.totalHours > 0 && (
                        <div className="flex justify-between">
                          <span className="text-gray-600">Hours:</span>
                          <span className="font-medium">{record.totalHours.toFixed(1)}</span>
                        </div>
                      )}
                    </div>
                  )}

                  <div className="flex gap-2">
                    {status === 'not-marked' && (
                      <>
                        <Button
                          size="sm"
                          onClick={() => handleCheckIn(worker)}
                          disabled={isProcessing}
                          className="flex-1"
                        >
                          {isProcessing ? 'Processing...' : 'Check In'}
                        </Button>
                        <Button
                          size="sm"
                          variant="destructive"
                          onClick={() => markAbsent(worker)}
                          disabled={isProcessing}
                          className="flex-1"
                        >
                          {isProcessing ? 'Processing...' : 'Mark Absent'}
                        </Button>
                      </>
                    )}

                    {status === 'checked-in' && (
                      <Button
                        size="sm"
                        onClick={() => handleCheckOut(worker)}
                        disabled={isProcessing}
                        className="w-full"
                      >
                        {isProcessing ? 'Processing...' : 'Check Out'}
                      </Button>
                    )}

                    {(status === 'checked-out' || status === 'absent') && (
                      <div className="w-full text-center py-2 text-sm text-gray-600">
                        Attendance marked for today
                      </div>
                    )}
                  </div>
                </CardContent>
              </Card>
            );
          })}
        </div>
      ) : (
        <Card>
          <CardContent className="text-center py-12">
            <p className="text-gray-600 text-lg mb-4">No active workers found</p>
            <Button asChild>
              <a href="/workers/new">Add Workers</a>
            </Button>
          </CardContent>
        </Card>
      )}

      {/* Quick Actions */}
      <Card>
        <CardHeader>
          <CardTitle>Quick Actions</CardTitle>
        </CardHeader>
        <CardContent>
          <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
            <Button variant="outline" asChild className="justify-start">
              <a href="/workers">Manage Workers</a>
            </Button>
            <Button variant="outline" asChild className="justify-start">
              <a href="/attendance/history">View Attendance History</a>
            </Button>
            <Button variant="outline" asChild className="justify-start">
              <a href="/attendance/reports">Generate Reports</a>
            </Button>
          </div>
        </CardContent>
      </Card>
    </div>
  );
}
